@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
.App {
  background: #fff;
  color: #333;
  display: grid;
  grid-template-columns: 17rem 45rem auto;
  min-height: 100vh;
  transition: background 0.2s;
}

.side-container {
  height: 100vh;
}

.App--dark {
  background: #222;
  color: #eee;
}

/* Side Panel */

.App--dark .SidePanel::-webkit-scrollbar-thumb {
  background-color: #333;
}

.App--dark .SidePanel::-webkit-scrollbar-thumb:hover {
  background-color: #444;
}

.App--dark .UserPanel {
  background: #444;
  border: 1px solid #444;
}

.App--dark .UserPanel-info__settings img {
  width: 1.25rem;
}

.App--dark .UserPanel-settings-dropdown {
  background-color: #444;
  border: 1px solid #444;
}

.App--dark .UserPanel-settings-dropdown li:hover {
  background-color: #555;
}

.App--dark .settings-change-avatar {
  border-bottom: 1px solid #666;
}

/* Channels */

.App--dark .Channels:hover {
  background-color: #444;
}

.App--dark .Channels-add img:hover {
  background-color: #333;
}

.App--dark .Channel-counter {
  background-color: #333;
}

/* Add channel window */

.App--dark .add-channel-window {
  background-color: #444;
  border: none;
}

.App--dark .add-btn {
  background: #a3c2dd;
}

.App--dark .cancel-btn {
  background-color: #c58ba0;
}

/* Channel items */

.App--dark .channel-item:hover {
  background: #333;
}

.App--dark .active,
.App--dark .active:hover {
  background-color: #444;
}

/* Messages */

.App--dark .Messages-window {
  background-color: #333;
}

.App--dark .Messages-window::-webkit-scrollbar-thumb {
  background-color: #444;
}

.App--dark .Messages-window::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

/* Messages Form */

.App--dark .MessagesForm {
  background: #222;
}

.App--dark .MessagesForm-upload-media {
  background-color: #222;
}

.App--dark .MessagesForm-upload-media:hover {
  background: #333;
}

.App--dark .MessagesForm-input {
  background: #222;
  border: 2px solid #333;
  color: #eee;
}

.App--dark .MediaModal {
  background-color: #82d1d8;
}

/* Message */

.App--dark .Message {
  background: #222;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
}

.App--dark .Message__content {
  color: #eee;
}

.App--dark .Message__time {
  color: #bbb;
}

.App--dark .self-message {
  background: #433aff;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.App--dark .self-message-searched,
.App--dark .message-searched {
  background: #1c294d;
}

/* Messages header */

.App--dark .Messages-header {
  background-color: #444;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}

.App--dark .Messages-header-info__users {
  color: #999;
}

.App--dark .Messages-header-search button img {
  width: 2.4rem;
  height: 2.4rem;
  transform: translatey(0.1rem);
}

.App--dark .Messages-header-search button:hover {
  background: #333;
}

/* Search tab */

.App--dark .Messages-search-tab {
  background-color: #444;
  border-left: 1px solid #222;
}

.App--dark .Messages-search-tab::-webkit-scrollbar-thumb {
  background-color: #555;
}

.App--dark .Messages-search-tab-header {
  background: #333;
}

.App--dark .Messages-search-tab-header button img {
  width: 1.5rem;
  transform: translate(0.25rem, 0.3rem);
}

.App--dark .Messages-search-tab-header button:hover {
  background: #444;
}

.App--dark .Messages-search-tab-header input {
  background-color: #444;
  color: #eee;
}

.App--dark .search-tab-content-item {
  border-bottom: 1px solid #333;
  color: #eee;
}

.App--dark .search-tab-content-item:hover {
  background: #555;
}

/* MetaPanel */

.App--dark .MetaPanel {
  background-color: #444;
  border: 1px solid #444;
}

.App--dark .MetaPanel-field {
  border-bottom: 1px solid #333;
}

.App--dark .MetaPanel-field p {
  color: #999;
}

/* Skeleton */

.App--dark .Skeleton {
  background: #222;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
}

.App--dark .Skeleton::after {
  background-image: linear-gradient(
    to left,
    transparent,
    rgba(100, 100, 100, 0.4),
    transparent
  );
}

.App--dark .Skeleton__avatar {
  background: #333;
}

.App--dark .Skeleton-details__text {
  background: #333;
}

.OptionsPanel {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 1rem;
}

.OptionsPanel-theme {
  cursor: pointer;
  position: relative;
}

.OptionsPanel-theme img {
  width: 2rem;
}

.light-mode-icon {
  transform: scale(1.2);
}
.SidePanel {
  padding: 1rem;
  height: 100vh;
  overflow-y: scroll;
}

.SidePanel::-webkit-scrollbar {
  width: 0.5rem;
}

.SidePanel::-webkit-scrollbar-thumb {
  background-color: #eee;
  border-radius: 0.5rem;
  -webkit-transition: background 0.5s;
  transition: background 0.5s;
}

.SidePanel::-webkit-scrollbar-thumb:hover {
  background-color: #ddd;
  -webkit-transition: background 0.5s;
  transition: background 0.5s;
}

.SidePanel-title {
  font-family: "Comfortaa", "Source Sans Pro", sans-serif;
  color: #632ce2;
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center;
  transform: translateX(-1rem);
}

.title__icon img {
  width: 3rem;
  transform: translate(.7rem, .6rem);
}

.UserPanel {
  background-color: #f3f7f8;
  border: 1px solid #d4d8d9;
  border-radius: 1rem;
  padding: 1.5rem;
  position: relative;
  margin: 2rem 0;
}

.UserPanel-image img {
  border-radius: 50%;
  display: block;
  width: 5rem;
  margin: auto;
}

.UserPanel-info {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.UserPanel-info__name {
  font-size: 1.2rem;
  font-weight: 600;
  margin-right: 0.5rem;
}

.UserPanel-info__settings img {
  width: 1rem;
  transform: translateY(0.2rem);
  cursor: pointer;
}

.UserPanel-settings-dropdown {
  background-color: #f3f7f8;
  border: 1px solid #d4d8d9;
  border-radius: 0.5rem;
  position: absolute;
  left: 0;
  bottom: -7rem;
  width: 14.5rem;
  z-index: 1000;
  transform: translateY(-2rem) scale(0.5);
  opacity: 0;
  visibility: hidden;
  transition: transform 0.1s, opacity 0.1s, visibility 0.1s;
}

.UserPanel-settings-dropdown li {
  cursor: pointer;
  padding: 1rem;
}

.UserPanel-settings-dropdown li:hover {
  background: #d4d8d9;
}

.settings-change-avatar {
  border-bottom: 1px solid #d4d8d9;
  border-radius: 0.5rem 0.5rem 0 0;
}

.settings-signout {
  border-radius: 0 0 0.5rem 0.5rem;
}

.settings-icon {
  width: 1rem;
  margin-right: 0.5rem;
  transform: translateY(0.2rem);
}

/* Channels */
.Channels-component {
  position: relative;
}

.Channels {
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em;
  cursor: pointer;
}

.Channels:hover {
  background: #f3f7f8;
}

.Channels-name {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.25rem;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  position: relative;
}

.Channels-name img {
  width: 0.7rem;
  margin-right: 0.5rem;
  transform: translateY(0.2rem);
}

.Channels-add img {
  border-radius: 50%;
  width: 1.7rem;
  padding: 0.3rem;
  transform: translateY(0.1rem);
}

.Channels-add img:hover {
  background-color: #d4d8d9;
}

.Channel-counter {
  background: #d4d8d9;
  border-radius: 50%;
  text-align: center;
  font-size: 0.95rem;
  position: absolute;
  padding-top: 0.15rem;
  right: -1.7rem;
  top: 0;
  width: 1.3rem;
  height: 1.3rem;
}

.notification {
  background: red;
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
}

/* Add Channel Window */
.add-channel-window {
  background-color: #f3f7f8;
  border: 1px solid #d4d8d9;
  border-radius: 0.5rem;
  padding: 1rem;
  position: absolute;
  top: 3.2rem;
  transform: translateY(-2rem) scale(0.5);
  opacity: 0;
  visibility: hidden;
  transition: transform 0.1s, opacity 0.1s, visibility 0.1s;
}

.add-channel-form input {
  background-color: #333;
  border: none;
  border-radius: 0.5rem;
  color: #eee;
  padding: 0.5rem;
  margin-bottom: 0.3rem;
  outline: none;
  width: 100%;
}

.add-channel-form input::-webkit-input-placeholder {
  color: #999;
}

.add-channel-form input:-ms-input-placeholder {
  color: #999;
}

.add-channel-form input::-ms-input-placeholder {
  color: #999;
}

.add-channel-form input::placeholder {
  color: #999;
}

.add-channel-buttons {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 0.5rem;
}

.add-channel-buttons button {
  font-size: 0.8rem;
  padding: 0;
  width: 4rem !important;
  height: 1.5rem;
  outline: none;
}

.add-btn {
  background: #bde0fe;
}

.cancel-btn {
  background-color: #ffafcc;
}

/* Channel Items */
.Channel-items {
  padding: 0.5rem 1rem;
}

.channel-item {
  border-radius: 0.5rem;
  padding: 0.5rem;
  position: relative;
  cursor: pointer;
}

.channel-item:hover {
  background: #f3f7f8;
}

.channel-item img {
  width: 0.8rem;
  transform: translateY(0.1rem);
  margin-right: 0.3rem;
}

.avatar-img {
  border-radius: 50%;
  width: 1rem !important;
}

.status-circle {
  border-radius: 50%;
  position: absolute;
  right: 1rem;
  top: 0.85rem;
  width: 0.5rem;
  height: 0.5rem;
}

.online {
  background: #9fffd7;
}

.offline {
  background: #ff9bc0;
}

.active,
.active:hover {
  background: #d4d8d9;
}

.Messages {
  padding: 1rem;
  height: 100vh;
  position: relative;
}

.Messages-window {
  background: #f3f7f8;
  border-radius: 1rem;
  padding-bottom: 1rem;
  position: relative;
  overflow-y: scroll;
  height: 100%;
}

.Messages-window::-webkit-scrollbar {
  width: 0.5rem;
}

.Messages-window::-webkit-scrollbar-thumb {
  background-color: #ddd;
  border-radius: 0.5rem;
}

.Messages-window::-webkit-scrollbar-thumb:hover {
  background-color: #ccc;
}

.Messages-content-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 90%;
}

/* Form */

/* form is fixed regarding to the Messages-window (sticky position) */
.MessagesForm {
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 -3px 5px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 1rem;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  width: 95%;
  margin: 1rem auto 0 auto;
}

.MessagesForm-upload-media {
  background: #fff;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  position: relative;
  width: 40px;
  height: 40px;
  transition: background 0.2s;
}

.MessagesForm-upload-media:hover {
  background: #f3f7f8;
}

.MessagesForm-upload-media img {
  width: 1rem;
  transform: translateY(0.1rem);
}

.MessagesForm-input-container {
  width: 70%;
  position: relative;
}

.MessagesForm-input {
  border: 2px solid #ddd;
  border-radius: 0.2rem;
  height: 2.5rem;
  padding-left: 1rem;
  outline: none;
  width: 100%;
  margin: auto;
}

.MessagesForm-emoji-picker {
  background: transparent;
  border: none;
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  right: 1rem;
  top: 0.6rem;
  cursor: pointer;
  outline: none;
}

.MessagesForm-emoji-picker img {
  height: 1.2rem;
  width: 1.2rem;
}

.emoji-picker {
  position: absolute;
  top: -28rem;
  right: 0;
}

.MessagesForm-send {
  background: #7a66ea;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 0.2rem;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2rem;
  outline: none;
  width: 5rem;
  transition: background 0.2s;
}

.MessagesForm-send:hover {
  background: #2363ff;
  background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.MessagesForm-send img {
  width: 1rem;
  margin-left: 0.5rem;
}

.disabled {
  background: #2363ff;
  background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  cursor: default;
}

/* Message */

.Messages-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  margin-top: 1rem;
}

.Message {
  background: #fff;
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0 3px 5px #e3e7e8;
  display: flex;
  font-weight: 300;
  align-items: center;
  padding: 1rem;
  padding-bottom: 1.5rem;
  position: relative;
  min-height: 3.5rem;
  max-width: 400px;
  margin-bottom: 1rem;
  margin-left: 1rem;
  transition: background 0.2s;
}

.Message__avatar img {
  border-radius: 50%;
  width: 2.5rem;
}

.Message__avatar {
  margin-right: 1rem;
}

.Message__content {
  color: #333;
  max-width: 38ch;
  word-break: break-all;
}

.Message__time {
  color: #666;
  font-size: 0.8rem;
  position: absolute;
  right: 0.5rem;
  bottom: 0.5rem;
}

.self-message {
  align-self: flex-end;
  background: #433aff;
  background: linear-gradient(155deg, #534aff, #9650ff);
  box-shadow: 0 3px 5px #bbbebf;
  flex-direction: row-reverse;
  margin-right: 1rem;
}

.self-message .Message__avatar {
  margin-left: 1rem;
  margin-right: 0;
}

.self-message .Message__content {
  color: #eee;
  margin-left: 0;
}

.self-message .Message__time {
  color: #ccc;
  left: 0.5rem;
}

.Message__image {
  border-radius: 0.2rem;
  padding-bottom: 0.5rem;
  max-width: 300px;
}

.image-wrapper .Message__avatar {
  align-self: flex-start;
}

.self-message-searched {
  background: #273969;
}

.message-searched {
  background: rgb(255, 249, 216);
}

/* MediaModal */

.MediaModal {
  background: #9bf6ff;
  border-radius: 50%;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 4rem;
  top: -6rem;
  left: -0.6rem;
  position: absolute;
  outline: none;
  transform: translateY(2rem) scale(0.5);
  opacity: 0;
  visibility: hidden;
  transition: transform 0.2s, opacity 0.2s, visibility 0.1s;
}

.MediaModal img {
  width: 1.5rem;
  transform: translateY(-0.1rem);
}

/* Messages Header */

.Messages-header {
  background: #e2e5e6;
  border-radius: 1rem 1rem 0 0;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 1000;
  margin-bottom: 1rem;
  height: 4rem;
}

.Messages-header-info__channel-name {
  font-size: 1.4rem;
  margin-bottom: 0.2rem;
}

.channel-name__icon {
  width: 1.5rem;
  margin-left: 0.5rem;
  transform: translateY(0.2rem);
  cursor: pointer;
}

.Messages-header-info__users {
  font-size: 0.9rem;
  color: #666;
}

.Messages-header-search {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Messages-header-search button {
  background: inherit;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  width: 2.5rem;
  height: 2.5rem;
}

.Messages-header-search button:hover {
  background: #cacecf;
}

.Messages-header-search button img {
  width: 1.8rem;
}

/* Search tab */

.Messages-search-tab {
  background: #f3f7f8;
  border-left: 1px solid #c2c2c2;
  border-radius: 0 1rem 0 0;
  width: 50%;
  height: 95%;
  position: absolute;
  right: 1rem;
  top: 0;
  margin-top: 1rem;
  z-index: 9999;
  transform: translateX(75%);
  visibility: hidden;
  opacity: 0;
  transition: transform 0.2s, opacity 0.1s, visibility 0.1s;
  overflow-y: scroll;
}

.Messages-search-tab::-webkit-scrollbar {
  width: 0.5rem;
}

.Messages-search-tab::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 0.5rem;
}

.open-tab {
  transform: translateX(0);
  opacity: 1;
  visibility: visible;
}

.Messages-search-tab-header {
  background: #d4d8d9;
  border-radius: 0 1rem 0 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 4rem;
}

.Messages-search-tab-header input {
  background: #f3f7f8;
  border: none;
  border-radius: 1rem;
  height: 2rem;
  width: 70%;
  padding: 1rem;
  outline: none;
}

.Messages-search-tab-header button {
  background: inherit;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  width: 2rem;
  height: 2rem;
}

.Messages-search-tab-header button img {
  transform: translateY(0.1rem);
}

.Messages-search-tab-header button:hover {
  background: #cacecf;
}

.search-tab-content-item {
  border-bottom: 1px solid #c2c2c2;
  color: #333;
  font-weight: 200;
  padding: 2rem;
  position: relative;
  cursor: pointer;
}

.search-tab-content-item:hover {
  background: #dce0e0;
}

.search-tab-content-item__mesage {
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
}

.search-tab-content-item__date {
  font-size: 0.8rem;
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
}

/* Typing */

.user-typing {
  font-style: italic;
  font-weight: 300;
  margin: 2rem 1rem;
}

.Typing {
  background: #fff;
  border-radius: 1rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 3rem;
  height: 1.5rem;
}

.Typing__dot {
  background: #999;
  border-radius: 50%;
  width: 0.5rem;
  height: 0.5rem;
  opacity: 0;
  -webkit-animation: loadingFade 1s infinite;
          animation: loadingFade 1s infinite;
}

.Typing__dot:nth-child(1) {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.Typing__dot:nth-child(2) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.Typing__dot:nth-child(3) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

@-webkit-keyframes loadingFade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 0;
  }
}

@keyframes loadingFade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 0;
  }
}

/* Skeleton */

.Skeleton {
  background: #d4d8d9;
  border-radius: 0.5rem;
  box-shadow: 0 3px 5px #e3e7e8;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  height: 4rem;
  margin: 0 1rem 1rem 1rem;
  padding: 1rem;
  width: 16rem;
}

.Skeleton::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  -webkit-animation: sweep 2s infinite;
          animation: sweep 2s infinite;
  background-image: linear-gradient(
    to left,
    transparent,
    rgba(255, 255, 255, 0.4),
    transparent
  );
}

@-webkit-keyframes sweep {
  0% {
    transform: translateX(-100%);
  }

  50% {
    transform: translateX(150%);
  }

  100% {
    transform: translateX(-100%);
  }
}

@keyframes sweep {
  0% {
    transform: translateX(-100%);
  }

  50% {
    transform: translateX(150%);
  }

  100% {
    transform: translateX(-100%);
  }
}

.Skeleton__avatar {
  background: #f3f7f8;
  border-radius: 50%;
  height: 2.5rem;
  width: 2.5rem;
}

.Skeleton-details {
